.wrapper {
  font-family: 'Press Start 2P', sans-serif;
  color: #eee;
  background-color: #222;
}

.win {
  background-color: #60b347;
}

.lost {
  background-color: chocolate;
}

.header {
  position: relative;
  height: 35vh;
  border-bottom: 7px solid #eee;
}

.main {
  height: 65vh;
  display: flex;
  align-items: center;
  justify-content: space-around;
}

/* ELEMENTS STYLE */
h1 {
  font-size: 4rem;
  text-align: center;
  position: absolute;
  width: 100%;
  top: 52%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.between {
  font-size: 1.4rem;
  position: absolute;
  top: 2rem;
  right: 2rem;
}

.guess {
  background: none;
  border: 4px solid #eee;
  font-family: inherit;
  color: inherit;
  font-size: 5rem;
  padding: 2.5rem;
  width: 25rem;
  text-align: center;
  display: block;
  margin-bottom: 3rem;
}
